@import url(https://fonts.googleapis.com/css2?family=Fira+Sans+Condensed&display=swap);
.app{padding:10px;max-width:480px;margin:auto;background-color:#eee;border:2px solid #444;color:#444;text-align:center;font-family:"Fira Sans Condensed",sans-serif}.title{-webkit-margin-after:0.1rem;margin-block-end:0.1rem}.author{margin:2px}.task-list{margin:1rem auto;text-align:left;width:15rem}.task{margin:0.5rem}.task-name{margin-left:1rem;margin-right:1rem}.task-completed{text-decoration:line-through;color:#888;transition:0.5s}.complete-checkbox{margin-right:1rem}button{margin:10px;border-radius:0px;background-color:#444;border:1px solid #444;color:#eee;font-family:"Fira Sans Condensed",sans-serif;transition-duration:0.4s;cursor:pointer}button:hover{border-radius:0px;background-color:#eee;color:#444}button.delete-button{margin:0px}.error{color:#cd5c5c}

